import { Component, OnInit } from '@angular/core';
import { MasterRepo } from '../../../../common/repositories';
import { AlertService } from '../../../../common/services/alert/alert.service';
import { AuthService } from '../../../../common/services/permission/authService.service';
import { SpinnerService } from '../../../../common/services/spinner/spinner.service';


@Component({
    selector: 'depreciation-detail',
    template: require('./depreciation-detail.component.html'),
    styles: [require('./depreciation-detail.component.css')]
})

export class DepreciationDetailsComponent implements OnInit {
    accountWiseData: any[] = [];
    itemWiseData: any[] = [];
    assetCodeWiseData: any[] = [];
    PhiscalObj: any = {};

    allTabData: any = {
        accountWiseData: [],
        itemWiseData: [],
        assetCodeWiseData: [],
    };

    monthMap: Map<string, number> = new Map<string, number>();
    detailsObj: any;
    RefNo: any;
    bodyData: any;
    constructor(
        private masterService: MasterRepo, private loader: SpinnerService, private alertService: AlertService, private authService: AuthService
    ) {
        this.isMonthSelected = false;

        this.months.forEach((month) => {
            this.monthMap.set(month.monthName, month.monthId);
        });

        this.PhiscalObj = this.masterService.PhiscalObj;
    }

    ngOnInit(): void {

    }
    months: Month[] = [
        { monthId: 1, monthName: 'Shrawan' },
        { monthId: 2, monthName: 'Bhadra' },
        { monthId: 3, monthName: 'Asoj' },
        { monthId: 4, monthName: 'Kartik' },
        { monthId: 5, monthName: 'Mangsir' },
        { monthId: 6, monthName: 'Poush' },
        { monthId: 7, monthName: 'Magh' },
        { monthId: 8, monthName: 'Falgun' },
        { monthId: 9, monthName: 'Chaitra' },
        { monthId: 10, monthName: 'Baisakh' },
        { monthId: 11, monthName: 'Jestha' },
        { monthId: 12, monthName: 'Asaar' },
    ];

    selectedMonth: string = '';
    isMonthSelected: boolean = false;
    calculationDone: boolean = false;
    isDeleteEnable: boolean = true;
    currentTab: string = 'account-wise';
    selectedTabExcel: number;
    selectedMonthId: number;
    isViewed: boolean = false;



    // totals for each tab
    totalCostValueAccountWise: number;
    totalBookValueAccountWise: number;
    totalDepreciationAccountWise: number;
    totalNetBookValueAccountWise: number;

    totalCostValueItemWise: number;
    totalBookValueItemWise: number;
    totalDepreciationItemWise: number;
    totalNetBookValueItemWise: number;
    totalQuantityItemWise: number;
    //totalCostItemWise: number;

    totalCostValueAssetCodeWise: number;
    totalBookValueAssetCodeWise: number;
    totalDepreciationAssetCodeWise: number;
    totalNetBookValueAssetCodeWise: number;




    onMonthSelected() {
        this.isMonthSelected = !!this.selectedMonth;

        if (this.isMonthSelected) {
            this.selectedMonthId = this.monthMap.get(this.selectedMonth);
        } else {
            this.selectedMonthId = undefined;
        }
    }

    calculate() {
        this.loader.show('Please wait..');
        this.masterService.getDepreciationDetail(this.selectedMonthId, this.PhiscalObj.PhiscalID).subscribe(
            (res: DepreciationDetail) => {

                this.loader.hide();
                this.accountWiseData = res.result; // Store data for account-wise tab
                this.itemWiseData = res.result2; // Store data for item-wise tab
                this.assetCodeWiseData = res.result3;

                if (this.currentTab === 'account-wise') {
                    this.accountWiseData = res.result;
                } else if (this.currentTab === 'item-wise') {
                    this.itemWiseData = res.result2;
                } else if (this.currentTab === 'asset-code-wise') {
                    this.assetCodeWiseData = res.result3;
                }

                this.calculateTotalsAccountWise();
                this.calculateTotalsItemWise();
                this.calculateTotalsAssetCodeWise();
                this.calculationDone = true;
                this.isDeleteEnable = true;
            }, error => {
                this.loader.hide();
                this.alertService.info(error._body);

            }
        );
    }

    // totals for 'account-wise' tab
    calculateTotalsAccountWise() {
        this.totalCostValueAccountWise = this.calculateTotal('Cost_Value', this.accountWiseData);
        this.totalBookValueAccountWise = this.calculateTotal('Current_Book_Value', this.accountWiseData);
        this.totalDepreciationAccountWise = this.calculateTotal('Depreciation', this.accountWiseData);
        this.totalNetBookValueAccountWise = this.calculateTotal('Net_Book_Value', this.accountWiseData);
    }

    // totals for 'item-wise' tab
    calculateTotalsItemWise() {
        this.totalCostValueItemWise = this.calculateTotal('Cost_Value', this.itemWiseData);
        this.totalQuantityItemWise = this.calculateTotal('Quantity', this.itemWiseData);
        // this.totalCostItemWise = this.calculateTotal('TotalCost', this.itemWiseData);
        this.totalBookValueItemWise = this.calculateTotal('Current_Book_Value', this.itemWiseData);
        this.totalDepreciationItemWise = this.calculateTotal('Depreciation', this.itemWiseData);
        this.totalNetBookValueItemWise = this.calculateTotal('Net_Book_Value', this.itemWiseData);
    }

    // totals for 'asset-code-wise' tab
    calculateTotalsAssetCodeWise() {
        this.totalCostValueAssetCodeWise = this.calculateTotal('Cost_Value', this.assetCodeWiseData);
        this.totalBookValueAssetCodeWise = this.calculateTotal('Current_Book_Value', this.assetCodeWiseData);
        this.totalDepreciationAssetCodeWise = this.calculateTotal('Depreciation', this.assetCodeWiseData);
        this.totalNetBookValueAssetCodeWise = this.calculateTotal('Net_Book_Value', this.assetCodeWiseData);
    }

    calculateTotal(column: string, data: any[]): number {
        let total = 0;

        for (const row of data) {
            const value = row[column];
            if (!isNaN(value)) {
                total += value;
            }
        }
        return total;
    }

    Reset() {
        this.selectedMonth = '';
        this.isMonthSelected = false;
        this.accountWiseData = [];
        this.itemWiseData = [];
        this.assetCodeWiseData = [];
        this.calculationDone = false;
        this.isViewed = false;
        this.isDeleteEnable = true;

        this.totalCostValueAccountWise = 0;
        this.totalBookValueAccountWise = 0;
        this.totalDepreciationAccountWise = 0;
        this.totalNetBookValueAccountWise = 0;
        this.totalCostValueItemWise = 0;
        this.totalQuantityItemWise = 0;
        // this.totalCostItemWise = 0;
        this.totalBookValueItemWise = 0;
        this.totalDepreciationItemWise = 0;
        this.totalNetBookValueItemWise = 0;
        this.totalCostValueAssetCodeWise = 0;
        this.totalBookValueAssetCodeWise = 0;
        this.totalDepreciationAssetCodeWise = 0;
        this.totalNetBookValueAssetCodeWise = 0;
    }

    Save() {
        this.loader.show("Saving data. Please wait...");

        this.masterService.saveDepreciationCalculation(`/SaveAssetDepreciation?currentmonth=${this.selectedMonthId}&phiscalyear=${this.PhiscalObj.PhiscalID}&configmode=1`, this.allTabData).subscribe(
            (res: any) => {
                if (res.status == "ok") {
                    this.loader.hide();

                    this.alertService.success(`Depreciation for the month ${this.selectedMonth} has been adjusted. Voucher No. ${res.refNo}`);
                    this.Reset();
                }
            },
            (error: any) => {
                this.loader.hide();
                this.alertService.error(error._body);
            }
        );
    }

    saveToExcel() {
        this.loader.show("Loading data. Please wait...");
        let filterObjData = { filtername: "AllDepreciationList" }

        this.masterService.getExcelFile(`/exportToExcel?currentmonth=${this.selectedMonthId}&phiscalyear=${this.PhiscalObj.PhiscalID}&configmode=1&type=${this.selectedTabExcel}`, filterObjData).subscribe(
            data => {
                this.masterService.downloadFile(data);
                this.alertService.success(`Depreciation for the month ${this.selectedMonth} has been adjusted.`);
                this.loader.hide();
            },
            (error: any) => {
                this.loader.hide();
                this.alertService.error(error._body);
            }
        )
    }

    View() {
        this.loader.show("Please wait...");

        this.masterService.viewDepreciationDetail(`/ViewAssetDepreciation?currentmonth=${this.selectedMonthId}&phiscalyear=${this.PhiscalObj.PhiscalID}&configmode=1`).subscribe(
            (res: any) => {
                if (res.status === "ok") {
                    this.loader.hide();
                    this.accountWiseData = res.result;
                    this.itemWiseData = res.result2;
                    this.assetCodeWiseData = res.result3;
                    this.calculationDone = true;

                    this.calculateTotalsAccountWise();
                    this.calculateTotalsItemWise();
                    this.calculateTotalsAssetCodeWise();
                    this.isViewed = true;
                    this.isDeleteEnable = false;
                    this.RefNo = res.RefNo;
                }
            },
            (error: any) => {
                this.loader.hide();
                this.alertService.error(error._body);
                this.Reset();
            }
        );
    }


    Cancel() {
        if (confirm("Are you sure you want to delete?")) {
            this.loader.show("Please wait...");

            let bodyData = {
                VoucherType: 12,
                VCHRNO: this.RefNo,
                MODE: "CANCELDEPRECIATION",
                VoucherPrefix: "JV"
            };

            this.masterService.cancelDepreciationDetail(`/cancelAcVouchers`, bodyData).subscribe(
                (res: any) => {
                    this.loader.hide();

                    if (res.status === "ok") {
                        this.alertService.success(`Voucher ${this.RefNo} has been canceled successfully.`);
                        this.Reset();
                    } else {
                        this.alertService.info(res.result.result);
                    }
                },
                error => {
                    this.loader.hide();

                    this.alertService.error(error._body);
                }
            );
        }
    }




    showTab(tabName: string) {
        this.currentTab = tabName;
        switch (tabName) {
            case 'account-wise': this.selectedTabExcel = 1;
                break;
            case 'item-wise': this.selectedTabExcel = 2;
                break;
            case 'asset-code-wise': this.selectedTabExcel = 3;
                break;
        }
    }
}



